/* global window */

/**
 * @customfunction
 * @requiresAddress
 */
export async function answer(question: string[][][], invocation: CustomFunctions.Invocation): Promise<string> {
  const fullQuestion = question.flat().flat().join("\n");
  return await window.responder
    .response(fullQuestion)
    .then(async (resp) => {
      await applyAnswerColor(resp.answer, invocation.address);
      return resp.explanation;
    })
    .catch((e) => Promise.reject(new CustomFunctions.Error(CustomFunctions.ErrorCode.invalidReference, e)));
}

async function applyAnswerColor(answer: string, address: string) {
  let color: string;
  switch (answer.toLowerCase()) {
    case "yes":
      color = "green";
      break;
    case "no":
      color = "red";
      break;
    case "unknown":
      color = "yellow";
      break;
    default:
      color = "white";
  }
  return await Excel.run(async (context) => {
    console.log("YELLOW CELL:" + address)
    const [worksheet, range] = address.split("!");
    const functionRange = context.workbook.worksheets.getItem(worksheet).getRange(range);
    functionRange.format.fill.color = color;
    return await context.sync();
  });
}

CustomFunctions.associate("ANSWER", answer);